import Vue from 'vue'
import VueRouter from 'vue-router'
// 导入刚才编写的组件
import AppIndex from '@/components/Appindex'
import Login from '@/components/Login'

Vue.use(VueRouter)
const routes = [
  {
    path:"/",
    redirect:"/login"
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/index',
    name: 'Appindex',
    component: AppIndex
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
